.hero {
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4)), url("../../assets/heroBG.jpg");
    background-size: cover;
    padding: 0 0 190px;

&-partners{
    display: flex;
    align-items: center;
    
}

&-block{
    display: flex;
    justify-content: space-between;
}

&-seach{
    padding-top: 100px;
    
    &-panel{
        height: 25px;
    }
}

&-title{
    font-style: normal;
    font-weight: normal;
    font-size: 72px;
    line-height: 84px;
    text-align: center;
      color: #504A4A;
    text-shadow: rgba(0, 0, 0, 0.25) 1px 1px 0, rgba(0, 0, 0, 0.25) -1px -1px 0, 
    rgba(0, 0, 0, 0.25) -1px 1px 0, rgba(0, 0, 0, 0.25) 1px -1px 0;;
    width: 1170px;    
    margin: 160px 100px 120px;
    }

&-link{
    display: flex;
    justify-content: space-around;
   
    &-platform{
        display: inline-block;
        text-decoration: none;
        text-align: center;
        width: 250px;
        height: 40px;
        font-weight: normal;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
        background: #FFCA28;
        padding: 20px 0 10px 0;
        margin-right: 20px;
            }
    &-enter{
        display: inline-block;
        text-decoration: none;
        text-align: center;
        width: 250px;
        height: 40px;
        font-weight: normal;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
        background: #FFCA28;
        padding: 20px 0 10px 0;
        margin-right: 20px;
                    }
    &-registration{
        display: inline-block;
        text-decoration: none;
        text-align: center;
        width: 250px;
        height: 40px;
        font-weight: normal;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
        background: #FFCA28;
        padding: 20px 0 10px 0;
        margin-right: 20px;
    }

    &-load{
        display: inline-block;
        text-decoration: none;
        text-align: center;
        width: 300px;
        height: 40px;
        font-weight: normal;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
        background: #FFCA28;
        padding: 20px 0 10px 0;
    }
}}
button{
    height: 30px;
}