.air-q {
    padding: 50px 0;
    background: #A79F9F;

    &-title {
    font-weight: 500;
    font-size: 36px;
    line-height: 42px;
    text-align: center;
    background: #FFCA28;
    color: #5F5050;

    }
  
    &-map {
      height: 680px;
      margin: 42px 0;
    }
  
    &-text {
      display: flex;
      justify-content: space-between;
       padding-top: 35px;

      &-new{
        width: 30px;
        height: 10px;
        background: #F80000;
        margin: 5px 5px 5px 0;
      }
      &-work{
        width: 30px;
        height: 10px;
        background: #F8DF00;
        margin: 5px 5px 5px 0;
      }
      &-done{
        width: 30px;
        height: 10px;
        background: #3ABC47;
        margin: 5px 5px 5px 0;
      }
      }
      &-link-button{
        display: flex;
        justify-content: space-around;
       
        &-new{
            display: inline-block;
            text-decoration: none;
            text-align: center;
            width: 350px;
            height: 40px;
            font-weight: normal;
            font-size: 24px;
            line-height: 28px;
            color: #5F5050;
            background: #FFCA28;
            padding: 20px 0 10px 0;
            margin-right: 110px;
                }
        &-work{
            display: inline-block;
            text-decoration: none;
            text-align: center;
            width: 350px;
            height: 40px;
            font-weight: normal;
            font-size: 24px;
            line-height: 28px;
            color: #5F5050;
            background: #FFCA28;
            padding: 20px 0 10px 0;
            margin-right: 110px;
                        }
        &-done{
            display: inline-block;
            text-decoration: none;
            text-align: center;
            width: 350px;
            height: 40px;
            font-weight: normal;
            font-size: 24px;
            line-height: 28px;
            color: #5F5050;
            background: #FFCA28;
            padding: 20px 0 10px 0;
                    }
      }
      &-point{
        display: flex;
        justify-content: space-around;
      }
  }
.p{
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #5F5050;

}