.about-platform{
    position: relative;
    background: #A79F9F;
    padding: 90px 65px 70px 15px;

&-title{
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
line-height: 42px;
    text-align: center;
    color: #5F5050;
    padding-bottom: 30px;

    &-content{
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
    }
}
&-block{
    padding: 25px 0;
}
&-back{
    background-color: #FFCA28;
    width: 240px;
    height: 185px;
    padding: 20px;
    border-radius: 35px;

}
&-icon{
    display: flex;
    justify-content: space-around;
    background: #A79F9F;
    
  
   &-item{
      display: flex;
      justify-content: space-around;
      width: 120px;
      height: 120px;
   }
   &-text{
    display: flex;
    justify-content: space-around;
   }
    
    }
    &-space{
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 28px;
        color: #5F5050;
        padding-bottom: 25px;

        &-item{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 16px;
            color: #5F5050;
                    }
    }
}
li
{
  list-style-type: disc;    
}