@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#A79F9F}.container{max-width:1280px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(255,255,255,0.4), rgba(255,255,255,0.4)),url(/birds/static/media/heroBG.ebc2c1d9.jpg);background-size:cover;padding:0 0 190px}.hero-partners{display:flex;align-items:center}.hero-block{display:flex;justify-content:space-between}.hero-seach{padding-top:100px}.hero-seach-panel{height:25px}.hero-title{font-style:normal;font-weight:normal;font-size:72px;line-height:84px;text-align:center;color:#504A4A;text-shadow:rgba(0,0,0,0.25) 1px 1px 0,rgba(0,0,0,0.25) -1px -1px 0,rgba(0,0,0,0.25) -1px 1px 0,rgba(0,0,0,0.25) 1px -1px 0;width:1170px;margin:160px 100px 120px}.hero-link{display:flex;justify-content:space-around}.hero-link-platform{display:inline-block;text-decoration:none;text-align:center;width:250px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0;margin-right:20px}.hero-link-enter{display:inline-block;text-decoration:none;text-align:center;width:250px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0;margin-right:20px}.hero-link-registration{display:inline-block;text-decoration:none;text-align:center;width:250px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0;margin-right:20px}.hero-link-load{display:inline-block;text-decoration:none;text-align:center;width:300px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0}button{height:30px}

.footer{border-top:1px solid #FFFFFF}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:80px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;text-decoration:none;color:#5F5050;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#5F5050;text-align:end}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#5F5050;margin-bottom:14px}.text-yellow{color:#EDE852}

.air-q{padding:50px 0;background:#A79F9F}.air-q-title{font-weight:500;font-size:36px;line-height:42px;text-align:center;background:#FFCA28;color:#5F5050}.air-q-map{height:680px;margin:42px 0}.air-q-text{display:flex;justify-content:space-between;padding-top:35px}.air-q-text-new{width:30px;height:10px;background:#F80000;margin:5px 5px 5px 0}.air-q-text-work{width:30px;height:10px;background:#F8DF00;margin:5px 5px 5px 0}.air-q-text-done{width:30px;height:10px;background:#3ABC47;margin:5px 5px 5px 0}.air-q-link-button{display:flex;justify-content:space-around}.air-q-link-button-new{display:inline-block;text-decoration:none;text-align:center;width:350px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0;margin-right:110px}.air-q-link-button-work{display:inline-block;text-decoration:none;text-align:center;width:350px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0;margin-right:110px}.air-q-link-button-done{display:inline-block;text-decoration:none;text-align:center;width:350px;height:40px;font-weight:normal;font-size:24px;line-height:28px;color:#5F5050;background:#FFCA28;padding:20px 0 10px 0}.air-q-point{display:flex;justify-content:space-around}.p{font-weight:normal;font-size:18px;line-height:21px;color:#5F5050}

.about-platform{position:relative;background:#A79F9F;padding:90px 65px 70px 15px}.about-platform-title{font-style:normal;font-weight:500;font-size:36px;line-height:42px;text-align:center;color:#5F5050;padding-bottom:30px}.about-platform-title-content{font-style:normal;font-weight:500;font-size:24px;line-height:28px;color:#5F5050}.about-platform-block{padding:25px 0}.about-platform-back{background-color:#FFCA28;width:240px;height:185px;padding:20px;border-radius:35px}.about-platform-icon{display:flex;justify-content:space-around;background:#A79F9F}.about-platform-icon-item{display:flex;justify-content:space-around;width:120px;height:120px}.about-platform-icon-text{display:flex;justify-content:space-around}.about-platform-space{font-style:normal;font-weight:500;font-size:24px;line-height:28px;color:#5F5050;padding-bottom:25px}.about-platform-space-item{font-style:normal;font-weight:500;font-size:14px;line-height:16px;color:#5F5050}li{list-style-type:disc}

